<template>
  <div id="edit-blog">
    <h2></h2>
    <form v-if="!submmited">
        <label></label>
        <input type="text" v-model="blog.title" required>
        <label></label>
        <textarea v-model="blog.content"></textarea>
        <div id="checkboxes">
            <label>Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label>Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label>React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label>Angular4</label>
            <input type="checkbox" value="Angular4" v-model="blog.categories">
        </div>
        <label></label>
        <select v-model="blog.author">
            <option v-for="author in authors" :key="author">
                {{author}}
            </option>
        </select>
        <button v-on:click.prevent="post"></button>
    </form>
    <div v-if="submmited">
        <h3></h3>
    </div>
    <div id="preview">
        <h3></h3>
        <p>{{blog.title}}</p>
        <p></p>
        <p>{{blog.content}}</p>
        <p></p>
        <ul>
            <li v-for="category in blog.categories" :key="category">
                {{category}}
            </li>
        </ul>
        <p>{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'edit-blog',
  data () {
    return {
        id:this.$route.params.id,
        blog:{},
        authors:["Jack","Stephen","Jone"],
        submmited:false
    }
  },
  methods:{
      fetchData(){
        //   console.log(this.id)
        this.$http.get("https://vuedemo-925a4.firebaseio.com/posts1/" + this.id + ".json")
            .then(response => {
                // console.log(response)
                this.blog = response.body;
            })
      },
      post(){
          this.$http.put("https://vuedemo-925a4.firebaseio.com/posts1/" + this.id + ".json",this.blog)
            .then((data) => {
                // console.log(data);
                this.submmited = true
            })
      }
  },
  created(){
      this.fetchData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#edit-blog *{
    box-sizing: border-box;
}
#edit-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin: 20px 0 10px;
}
input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
}
#checkboxes label{
    display: inline-block;
    margin-top: 0;
}
#checkboxes input{
    display: inline-block;
    margin-right: 10px;
}
button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
h3{
    margin-top: 10px;
}
textarea{
    height: 200px;
}
</style>



